html,
body {
  height: 100%;
  margin: 0;
}

.app {
  display: flex;
  flex-direction: column;
  min-height: 100vh;
}

* {
  font-family: "Raleway", sans-serif;
  font-optical-sizing: auto;
  font-weight: 400;
  font-style: normal;
}

body {
  background-color: #EBEBEB;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  text-align: center;
}

.link,
.link > a {
  color: #3676BD;
  text-decoration: none;
  cursor: pointer;
}

.loading-icon {
  font-size: 50px;
  color: #03153B;
}

.app {
  height: 100%;
}

/* MEDIA QUERIES FOR MOBILE */
@media (max-width: 425px) {
  .navbar-wrapper {
    background-color: #F4C522;
    height: 100%;
    width: 100%;
    display: grid;
    grid-template-columns: 1fr;
    position: sticky;
    top: 0%;
    z-index: 1000;
  }
  .navbar-wrapper .navlink-wrapper {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    gap: 10px;
    padding: 5px;
    font-size: 8px;
  }
  .navbar-wrapper .navlink-wrapper .link {
    text-decoration: none;
    color: #03153B;
    cursor: pointer;
    font-size: 12px;
  }
  .navbar-wrapper .navlink-wrapper .link:hover {
    color: #3676BD;
    cursor: pointer;
  }
  .navbar-wrapper .navlink-wrapper .active-link {
    color: #3676BD;
    border-bottom: 1px solid #3676BD;
  }
  .navbar-wrapper .search-wrapper {
    display: grid;
    grid-template-columns: 1fr;
    justify-content: center;
    align-items: center;
    position: relative;
  }
  .navbar-wrapper .search-wrapper .search-field {
    display: grid;
    grid-template-columns: 1fr;
    gap: 10px;
  }
  .navbar-wrapper .search-wrapper .search-field input {
    background-color: #F3E7BA;
    border: none;
    padding: 2px;
    align-self: center;
  }
  .navbar-wrapper .search-wrapper .search-field input:focus {
    outline: solid 2px #03153B;
  }
  .navbar-wrapper .search-wrapper .search-field input::placeholder {
    text-align: center;
    color: black;
    font-size: 10px;
    font-weight: 450;
  }
  .navbar-wrapper .search-wrapper .search-results {
    width: 94.5vw;
    max-height: 12vh;
    background-color: #F3E7BA;
    overflow: hidden;
    overflow-y: auto;
    position: absolute;
    top: 80%;
  }
  .navbar-wrapper .search-wrapper .search-results::-webkit-scrollbar {
    display: none;
  }
  .navbar-wrapper .search-wrapper .search-results > .link,
  .navbar-wrapper .search-wrapper .search-results p {
    text-align: center;
    font-size: 13px;
  }
  .navbar-wrapper .search-wrapper .search-results > .link:hover,
  .navbar-wrapper .search-wrapper .search-results p:hover {
    color: #03153B;
    background-color: #f4e087;
  }
  .navbar-wrapper .auth-wrapper {
    display: flex;
    flex-direction: row;
    justify-content: center;
    align-items: center;
    gap: 10px;
    margin-top: 5px;
    margin-bottom: 5px;
  }
  .navbar-wrapper .auth-wrapper button {
    background-color: #03153B;
    color: white;
    padding: 8px;
    border: none;
    padding: 3px;
  }
  .navbar-wrapper .auth-wrapper button:hover {
    background-color: #3676BD;
    cursor: pointer;
  }
  .navbar-wrapper .auth-wrapper button .link {
    text-decoration: none;
    color: #03153B;
    cursor: pointer;
    color: white;
    font-size: 10px;
  }
  .navbar-wrapper .auth-wrapper button .link:hover {
    color: #3676BD;
    cursor: pointer;
  }
  .navbar-wrapper .auth-wrapper button .link:hover {
    color: #EBEBEB;
  }
  .navbar-wrapper .auth-wrapper button .login > .link > .user-icon {
    color: #EBEBEB;
  }
  .navbar-wrapper .auth-wrapper button .login > .link > .user-icon:hover {
    color: #EBEBEB;
  }
}

